import { Box, Breadcrumbs, Container, Grid, Paper, Typography } from '@mui/material';
import { styled } from '@mui/styles';
import React from 'react';
import { Link, useParams } from 'react-router-dom';

import targetCar from '../../assets/images/targetCar.png';
import vehicle from '../../assets/images/vehicles.svg';
import { TitleBar } from '../../components';

export const VehiclesTarget: React.FC = () => {
    const params = useParams();
    return (
      <Container maxWidth={false}>
          <Grid item m="10px 0 20px">
              <Breadcrumbs aria-label="breadcrumb">
                  <Link color="inherit" to="/vehicles">Vehicle</Link>
                  <Typography color="textPrimary">{params.id}</Typography>
              </Breadcrumbs>
          </Grid>
          <Box sx={{display: 'flex'}}>
              <Grid item sx={{
                  minWidth: '438px',
                  border: '1px solid rgba(224, 224, 224, 0.1)'
              }}>
                  <TitleBar title="Target vehicle"/>
                  <StyledPaper>
                      <img src={targetCar} alt="Car"/>
                  </StyledPaper>
                  <ul>
                      <StyledList>
                          <Typography color="primary" variant="body2">Model:</Typography>
                          <Typography color="primary" variant="body2">Peugeot
                              308</Typography>
                      </StyledList>
                      <StyledList>
                          <Typography color="primary" variant="body2">VIN:</Typography>
                          <Typography>{params.id}</Typography>
                      </StyledList>
                      <StyledList>
                          <Typography color="primary" variant="body2">Last
                              update:</Typography>
                          <Typography>22/02/2021</Typography>
                      </StyledList>
                      <StyledList>
                          <Typography color="primary">Number of incidens:</Typography>
                          <Typography>12</Typography>
                      </StyledList>
                  </ul>
              </Grid>
              <StyledGrid item>
                  <TitleBar title="Vehicle network map"/>
                  <StyledImageWrapp>
                      <img src={vehicle} alt="Vehicle"/>
                  </StyledImageWrapp>
              </StyledGrid>
          </Box>
      </Container>
    );
};

// Todo Yusuf: Вынести styled в отдельный файл как у Sidebar

const StyledPaper = styled(Paper)({
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '482px',
    width: '100%'
});

const StyledList = styled('li')({
    display: 'flex',
    alignItems: 'center',
    height: '52px',
    maxWidth: '100%',
    width: '100%',
    borderTop: '1px solid rgba(224, 224, 224, 0.1)',
    '& p:first-child': {
        width: 160,
        marginLeft: '15px'
    },
    '& p:last-child': {
        marginLeft: 15
    }
});

const StyledGrid = styled(Grid)({
    maxWidth: '100%',
    width: '100%',
    marginLeft: 20,
    border: '1px solid rgba(224, 224, 224, 0.1)'

});

const StyledImageWrapp = styled(Paper)({
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    maxWidth: '100%',
    width: '100%',
    height: '100%',
    padding: '0 30px 0 0',
    '& img': {
        maxWidth: '95%',
        height: 'auto',
        display: 'block'
    }
});
