import { ThemeProvider } from '@mui/material';
import React from 'react';
import { HashRouter, Route, Routes } from 'react-router-dom';

import { AuthProvider, CommonShell } from './components';
import { theme } from './constants';
import {
  DetectorEditPage,
  DetectorPage,
  DetectorsPage,
  LoginPage,
  NotFoundPage,
  VehiclesPage,
  VehiclesTarget
} from './pages';

export const App: React.FC = () => (
  <HashRouter>
    <ThemeProvider theme={theme}>
      <AuthProvider>
        <Routes>
          <Route path="/" element={<CommonShell/>}>
            <Route index element={<VehiclesPage/>}/>
            <Route path="vehicles-target/:id" element={<VehiclesTarget/>}/>
            <Route path="detectors" element={<DetectorsPage/>}/>
            <Route path="detector/:id" element={<DetectorPage/>}/>
            <Route path="detector-editor/:id" element={<DetectorEditPage/>}/>
          </Route>
          <Route path="/login" element={<LoginPage/>}/>
          <Route path="*" element={<NotFoundPage/>}/>
        </Routes>
      </AuthProvider>
    </ThemeProvider>
  </HashRouter>
);
