import React from 'react';
import
{Box, Button, FormGroup, Checkbox, FormControlLabel, Alert, Chip, Stack, TextField, Typography} from '@mui/material';
import { styled } from "@mui/styles"
import {CheckBox, Delete, Edit} from "@mui/icons-material";

export const ActionsFields: React.FC = () => {

    return (
        <Box>
            <Stack flexDirection='row' alignItems='center' sx={{margin: '20px 0 20px 20px'}}>
                <Typography color='textSecondary' variant='body1'>
                    Action
                </Typography>
                <Button
                    variant='outlined'
                    sx={{margin:'0 20px', width: 200}}
                    size='large'
                    startIcon={<Edit color='primary' />}
                >
                    Edit
                </Button>
                <Button
                  variant='outlined'
                  size='large'
                  disabled
                  startIcon={<Delete/>}
                >
                    Delete user
                </Button>
            </Stack>
            <StyledContainer>
                <StyledBox>
                    <Stack sx={{width: 576}}>
                        <Typography variant='h5' >
                            Alert name and description
                        </Typography>
                        <TextField
                            label='Name'
                            placeholder='Alert name'
                            sx={{margin: '28px 0 16px'}}
                            fullWidth
                        />
                        <TextField
                            id='outlined-multiline-static'
                            label='Description'
                            placeholder='Alert description'
                            multiline
                            rows={8}
                            variant='outlined'
                        />
                    </Stack>
                    <Stack sx={{width: 192}}>
                        <Typography variant='h5' >
                            Impact rate
                        </Typography>
                        <FormGroup sx={{marginTop: '21px'}}>
                            <StyledForm
                                control={<Checkbox/>}
                                label={<Alert variant="filled" severity="error">Severe</Alert>}
                            />
                            <StyledForm
                                control={<Checkbox/>}
                                label={<Alert variant="filled" severity="warning">Major</Alert>}
                            />
                            <StyledForm
                                control={<Checkbox/>}
                                label={<Alert variant="filled" severity="info">Moderate</Alert>}
                            />
                            <StyledForm
                                control={<Checkbox/>}
                                label={<Alert variant="filled" severity="success">Negligible</Alert>}
                            />
                            <StyledForm
                                control={<Checkbox/>}
                                label={<Alert variant="outlined" severity="success">No impact</Alert>}
                            />
                        </FormGroup>
                    </Stack>
                    <Box  sx={{width: 620}}>
                        <Typography variant='h5' >
                            Trigger
                        </Typography>
                        <Stack flexDirection='row' alignItems='center' m='20px 0 12px'>
                            <StyledTrigger
                                disabled
                            >
                                Single
                            </StyledTrigger>
                            <StyledTrigger
                                disabled
                            >
                                Multi
                            </StyledTrigger>

                        </Stack>
                        <Typography variant='body1' color='textSecondary'>
                            Specify the time period for which the triggers will be counted, and their number
                        </Typography>
                        <Box m='20px 0'>
                            <Typography variant='h6'>
                                Detection period
                            </Typography>
                            <Stack mt='15px' direction="row" spacing={2}>
                                <Chip disabled label="30 min" color='primary' size='medium' />
                                <Chip disabled label="1 hours" variant="outlined" />
                                <Chip disabled label="2 hours" variant="outlined" />
                                <Chip disabled label="3 hours" variant="outlined" />
                                <Chip disabled label="4 hours" variant="outlined" />
                                <Chip disabled label="5 hours" variant="outlined" />
                            </Stack>
                        </Box>
                        <Box>
                            <Typography variant='h6' mb='15px'>
                                Detection threshhold
                            </Typography>
                            <TextField
                                disabled
                                variant='outlined'
                                label='Minimum count'
                                fullWidth
                            />
                        </Box>
                    </Box>
                </StyledBox>
            </StyledContainer>
        </Box>
    );
}

const StyledTrigger = styled(Button)({
    width: '132px',
    height: '30px',
    color: '#FF9800',
});

const StyledForm = styled(FormControlLabel)({
    marginBottom: '17px'
});

const StyledContainer = styled("div")({
    padding: '24px 16px 0 16px',
    borderTop: 'rgba(255, 255, 255, 0.33) solid 2px',
})

const StyledBox = styled(Box)({
    display: 'flex',
    justifyContent: 'space-between',
    paddingBottom: '22px',
    borderBottom: '1px solid rgba(133, 133, 133, 0.2)'
})