import CheckIcon from '@mui/icons-material/Check';
import StarOutlineIcon from '@mui/icons-material/StarOutline';
import WarningIcon from '@mui/icons-material/Warning';
import {Box, styled} from '@mui/material';
import { GridColDef, GridRenderCellParams } from '@mui/x-data-grid';
import React, { Fragment } from 'react';

import { IVehiclesProps } from '../types';
import {Link} from "react-router-dom";

/**
 * Стили для иконок таблицы Vehicles
 */

const CustomizedCheckIconBox = styled(Box)(() => `
  align-items: center;
  background-color: #4CAF50;
  border-radius: 50px;
  display: flex;
  height: 24px;
  justify-content: center;
  width: 24px;
  margin-right: 8px;
`);

const CustomizedWarningIcon = styled(WarningIcon)(() => `
  height: 19px;
  margin-right: 8px;
  width: 22px;
`);

/**
 * Пункты заголовков таблицы Vehicles
 */

export const vehiclesTableHeadName: GridColDef[] = [
  {field: 'id', hide: true},
  {
    field: 'noticed',
    headerName: 'Noticed',
    minWidth: 70,
    renderCell: (() => (
      <StarOutlineIcon/>
    ))
  },
  {
    field: 'vin',
    headerName: 'VIN',
    renderCell: (((params: GridRenderCellParams <IVehiclesProps>) =>
        <Link to={`/vehicles-target/${params.row.vin}`}>
          {params.row.vin}
        </Link>
    )),
    flex: .2
  },
  {field: 'brand', headerName: 'Brand', flex: .2},
  {field: 'model', headerName: 'Model', flex: .2},
  {field: 'last_updated', headerName: 'Last update', flex: .2},
  {field: 'alerts_number', headerName: 'Number of alerts', flex: .2},
  {
    field: 'is_checked',
    headerName: 'Status',
    flex: .08,
    renderCell: ((params: GridRenderCellParams<IVehiclesProps>) => params.value ? (
        <Fragment>
          <CustomizedCheckIconBox>
            <CheckIcon fontSize="small"/>
          </CustomizedCheckIconBox>
          Checked
        </Fragment>
      ) : (
        <Fragment>
          <CustomizedWarningIcon viewBox="0 0 22 19"/> New
        </Fragment>
      )
    )
  }
];

