import React from "react";
import {Box, FormGroup, TextField, Autocomplete, Stack, Typography, Chip, Button} from "@mui/material";

export const VehicleType: React.FC = () => {

    const brands = [
        { label: 'BMW' },
        { label: 'Toyota' }
    ]

    return (
        <Box sx={{padding: '0 16px'}}>
            <Typography variant='h5' m='40px 0 15px'>
                Vehicle type
            </Typography>
            <Typography variant='body1' color='textSecondary'>
                Specify vehicle
            </Typography>
            <FormGroup>
                <Stack flexDirection='row' alignItems='center' m='26px 0 35px'>
                    <Autocomplete
                        disablePortal
                        options={brands}
                        size='medium'
                        sx={{ width: 320 }}
                        renderInput={(params) => <TextField {...params} label="Brand" />}
                    />
                    <Autocomplete
                        multiple
                        id="tags-outlined"
                        options={brands}
                        getOptionLabel={(option) => option.label}
                        filterSelectedOptions
                        sx={{width: 340, m: '0 30px 0 20px'}}
                        renderTags={(value, getTagProps) =>
                            value.map((option, index) => (
                                <Chip
                                    variant="outlined"
                                    label={option.label}
                                    {...getTagProps({index})}
                                />
                            ))
                        }
                        renderInput={(params) => (
                            <TextField
                                {...params}
                                label="Model"
                                placeholder="Model"
                            />
                        )}
                    />
                    <Button
                        sx={{width: 120}}
                        variant='outlined'
                    >
                        Add
                    </Button>
                    <Button
                        sx={{width: 120, marginLeft: '15px'}}
                        variant='outlined'
                        disabled
                    >
                        Reset
                    </Button>
                </Stack>
                <Box>
                    <Stack flexDirection='row' alignItems='center' m='45px 0'>
                        <Autocomplete
                            disablePortal
                            options={brands}
                            size='medium'
                            sx={{ width: 320 }}
                            renderInput={(params) => <TextField {...params} label="Label" />}
                        />
                        <Autocomplete
                            disabled
                            multiple
                            id="tags-outlined"
                            options={brands}
                            getOptionLabel={(option) => option.label}
                            filterSelectedOptions
                            sx={{width: 340, m: '0 30px 0 20px'}}
                            renderTags={(value, getTagProps) =>
                                value.map((option, index) => (
                                    <Chip
                                        variant="outlined"
                                        label={option.label}
                                        {...getTagProps({index})}
                                    />
                                ))
                            }
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    label="Model"
                                    placeholder="Model"
                                />
                            )}
                        />
                    </Stack>
                </Box>
            </FormGroup>
        </Box>
    )
}