import {Breadcrumbs, Button, Container, Grid, Typography} from '@mui/material';
import React from 'react';
import { Link, useParams } from 'react-router-dom';

import { TitleBar } from '../../components/molecules';
import {ActionsFields} from "./fragments/_actionsFields";
import {VehicleType} from "./fragments/_vehicleType";
import {Logic} from "./fragments/_logic";

export const DetectorEditPage: React.FC = () => {
  const params = useParams();

  return (
    <Container maxWidth={false}>
      <Grid lg={12}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link color="inherit" to="/detectors">Detectors</Link>
          <Typography color="text.primary">{params.id}</Typography>
        </Breadcrumbs>
      </Grid>
      <Grid item>
        <TitleBar title={`Detector editor ID ${params.id} `}/>
      </Grid>
       <ActionsFields/>
        <VehicleType/>
        <Logic/>
    </Container>
  );
};
